import React from 'react';
import Button from "material-ui/es/Button/Button";

class MilestoneCheckButton extends React.Component {

    constructor(props) {
        super(props);
        this.modalLaunchFromButton = this.modalLaunchFromButton.bind(this);

    }


    modalLaunchFromButton(){
        this.props.modalLauch(this.props.data);
    }

    render() {
        return (
            <div>
                <Button color="secondary" onClick={this.modalLaunchFromButton}>{this.props.data["title"]}</Button>
            </div>
        );
    }
}

export default MilestoneCheckButton;


